@import "../../assets/scss/variables.scss";

.sidebar {
  display: none;
  height: 100%;
  width: 100%;
  z-index: 5;
  font-size: 15px;
  line-height: 17px;
  letter-spacing: -0.234375px;

  @media (min-width: $desktop-min-width) {
    display: block;
    box-shadow: 0px 1px 75px rgba(90, 97, 105, 0.11),
      0px 2px 4px rgba(90, 97, 105, 0.12), 0px 7.5px 11px rgba(90, 97, 105, 0.1),
      0px 3.5px 17.5px rgba(165, 182, 201, 0.1);
  }

  &__header {
    display: flex;
    column-gap: 11.5px;
    align-items: center;
    color: $blue-gray;
    font-size: 16px;
    line-height: 18px;
    padding: 23px 49px;
    box-shadow: 0px 1px 0px $shadow-gray;

    svg {
      width: 22px;
      height: 22px;
    }
  }

  & .menu {
    margin-top: 1px;

    &__item {
      display: block;
      text-decoration: none;
      color: $blue-gray;
      padding: 20px 32px;

      svg {
        width: 14px;
        height: 14px;
        margin-right: 13.5px;
      }

      box-shadow: inset 0px -1px 0px $shadow-gray;

      &:hover,
      &:focus {
        color: $blue;

        svg path {
          fill: $blue;
        }
      }

      &.active,
      &:active {
        background-color: $bg-admin-light;
        box-shadow: inset 0px -1px 0px $shadow-gray, inset 4px 0px 0px #007bff;
      }
    }
  }
}
