@import "../../assets/scss/variables.scss";

.main-header {
  box-shadow: 0px 2.5px 9.5px rgba(90, 97, 105, 0.12);

  & .main-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    @media (min-width: 479px) {
      height: 68px;
      flex-wrap: nowrap;
      justify-content: space-between;
    }
  }
}

.search {
  display: flex;
  align-items: center;

  &__button {
    width: 14px;
    height: 50%;
    cursor: pointer;
    margin-right: 9px;

    &:hover path,
    &:focus path {
      fill: $blue;
    }
  }
}

.profile {
  display: flex;

  &__notifications {
    padding: 23px 26px;

    @media (min-width: 479px) {
      box-shadow: 0.5px 0px 0px #cacedb, -0.5px 0px 0px #cacedb;
    }

    box-shadow: 0.5px 0px 0px #cacedb;

    &:hover,
    &:focus {
      background-color: $bg-admin-light;

      svg path {
        fill: $blue;
      }
    }

    & .count-notify {
      position: relative;
      bottom: 50%;
      left: 60%;

      font-family: "Poppins";
      font-style: normal;
      font-weight: 600;
      font-size: 9px;
      text-decoration: none;
      text-align: center;
      color: $white;
      background-color: $red;
      border-radius: 50%;
    }
  }

  &__menu {
    margin-left: 1px;
  }

  & .user-menu {
    position: relative;
    display: grid;
    grid-template-columns: 55px 1fr 10px;
    align-items: center;
    padding-left: 25px;
    min-width: 168px;

    &:hover,
    &:focus {
      background-color: $bg-admin-light;

      & .user-menu__dropdown-icon path {
        fill: $blue;
      }
    }

    &__image {
      width: 42px;
      height: 42px;
      margin-right: 13px;
    }

    &__username {
      color: $blue-gray;
      font-size: 15px;
      line-height: 17px;
      letter-spacing: -0.471429px;
    }

    & .dropdown {
      position: absolute;
      z-index: 2;
      top: 68px;
      left: 0;
      right: 0;
      background-color: $white;
      border: 0.5px solid $shadow-gray;
      border-bottom: 0;

      &__item {
        display: block;
        text-decoration: none;
        color: $blue-gray;
        padding: 20px 32px;

        box-shadow: inset 0px -1px 0px $shadow-gray;

        &:hover,
        &:focus {
          color: $blue;
          background-color: $bg-admin-light;
        }
      }
    }
  }
}
